img {
  max-width: 100%; }

img.logo {
	height: 65px;
	margin: 10px 0 0 39px; }

section img {
	width: auto;
	max-height: 65vh; }

.caption { // Use captions on standalone section images
  color: #999;
  font-family: 'Vollkorn', serif;
  font-size: 0.84rem;
  margin-bottom: 30px;
  margin-left: 4px; }

@media screen and (max-width: 739px) {
  .caption {
    margin-top: -4px; } }


.thumbnail {
  padding: 0;
  margin: 1px 3px 1px 0;
  img {
    display: inline-block;
    height: auto; }
  &:hover {
    img {
      opacity: 0.5; } } }

@media screen and (max-width: 739px) {
  .thumbnail {
    margin: 0; // Reset for mobile display so that captions underneath thumbnail and regular section images have the same spacing
    img {
      width: 100%;
      border-radius: 0; } } }

