// ------------------------------------------------------------------------
// Lightbox
// Adapted from Greg Bowler on Code Pen: https://codepen.io/g105b/pen/xuclB
// ------------------------------------------------------------------------

.lightbox { // Each thumbnail needs a corresponding lightbox
  display: none;

  &:target {
    display: block; }

  a {
    position: fixed;
    display: flex;
    background: #000;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    z-index: 8;


    &::after {
      content: "\2715";
      position: fixed;
      z-index: 10;
      top: 0;
      right: 0;
      left: 0;
      background: rgba(0,0,0,0.5);
      text-align: right;
      font-size: 2.5em;
      font-weight: lighter;
      padding: 0 20px 0 0;
      color: #fff; }


    img {
      display: block;
      z-index: 9;
      max-height: none;
      height: 100%;
      width: auto;
      margin: auto;
      border-radius: 0; } } }

@media screen and (max-width: 739px) {
  .lightbox {
    a {
      img {
        height: auto;
        max-width: 100vw; } } } }

.lightbox-caption {
  font-family: 'Vollkorn', serif;
  position: fixed;
  z-index: 11;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100vw;
  height: auto;
  text-align: center;
  background: rgba(0,0,0,0.85);
  color: #fff;
  padding: 20px; }

@media screen and (max-width: 739px) {
  .lightbox-caption {
    padding: 10px 20px;
    font-size: 0.95rem;
    line-height: 1.4;
    text-align: left; } }

@-webkit-keyframes Show {
  from {
    opacity: 0;
    zoom: 2; } }




